<template>
  <div class="tab-bar" >
      <div class="box">
          <router-link class="bar" to="/home">
               <div class="box-item">
              <div class="imgbox">
                  <i class="el-icon-s-home" ></i>
              </div>
              <div class="text">首页</div>
          </div>
          </router-link>
        <router-link class="bar" to="/search">
               <div class="box-item">
              <div class="imgbox">
                  <i class="el-icon-search"></i>
              </div>
              <div class="text">发现</div>
          </div>
          </router-link>
         <router-link class="bar" to="/haibao"> 
              <div class="box-item">
              <div class="imgbox">
                  <i class="el-icon-s-promotion"></i>
              </div>
              <div class="text">海报</div>
          </div>
          </router-link>
          <router-link class="bar" to="cart">
               <div class="box-item">
              <div class="imgbox">
                  <i class="el-icon-shopping-cart-2"></i>
              </div>
              <div class="text">购物袋</div>
          </div>
             </router-link>
         <router-link class="bar" to="user">
               <div class="box-item">
              <div class="imgbox">
                  <i class="el-icon-user-solid"></i>
              </div>
              <div class="text">我的</div>
          </div>
             </router-link>
      </div>
	</div>
</template>

<script>
export default {
	props: {
		color: {
		
		}
	},
	data() {
		return {
		}

	}
}
</script>

<style scoped>
a{
    text-decoration: none;
}
.router-link-active{
    text-decoration: none;
    color: black  !important;
} 

.bar{
    width: 20%;
    color:rgba(121, 120, 120, 0.7);
    cursor: pointer;
    padding-top:1.1rem ;
    padding-bottom:1.1rem ;
}
.tab-bar{
    width:100%;
    position:fixed;
    bottom:0px;
    box-sizing: border-box;
    background: white;

}
.text{
    font-size: 0.4rem;
    font-weight: 1000;
    text-align: center;
}
.box{
    display: flex;
  
    justify-content: space-around;
    justify-content: center;
    align-items: center;

}
.box-item{
           
 align-items: center;
 justify-items: center;
}
.imgbox{
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>


