<template>
<div class="">
          <div class="">
              <el-drawer
        :visible.sync="search.detail"
        :direction="search.direction"
        :show-close="false"
         size= 100%
        :before-close="handleClose"
        >   
        <div class="content">
        <div class="top">
                <div class="back" @click="back()">
                    <i class="el-icon-arrow-left"></i>我的鞋店
            </div>
        </div>
        <div class="search">
              <el-input
                placeholder="复古篮球鞋"
                v-model="searchtext">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
            <div class="right">{{goods.length}}件相关商品</div>
        </div>
        <div class="goodscontent" v-if="goods.length!=0">
            <div class="goods" v-for="item in goods" :key="item.id" @click="open(item.id)">
                <div class="search-content">
                    <img  class="goodsimg" :src="item.img">
                </div>
                <div class="bottom">
                    <div class="name">{{item.name}}</div>
                    <div class="price">￥{{item.price}}</div>
                </div>
            </div>
        </div>
        <div class="nogoodscontent" v-show="goods.length==0">
            <h2>很抱歉</h2>
            <div class="">我们没有找到,您搜索的相关的内容</div>
        </div>
        </div> 
    </el-drawer>
  <detail :detail="{detail}" v-on:getcart="res()"></detail>
    </div>
       
</div>
  
</template>
<style scoped>
.nogoodscontent{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.right{
    margin-top:0.5rem;
    font-size: 0.6rem;
    color: rgb(209, 209, 209);
    float:right;
}
.bottom{
    align-items: center;
}
.price{
    margin-top:1rem;
}
.name{
    width: 100%;
    margin-top:2rem;
}
.goodscontent{
    display: flex;
        flex-wrap: wrap;
        padding:2%;
        box-sizing: border-box;
        height: 88%;
        overflow: scroll;
}
.goods{
    width: 46%;
    height: 17rem;
    margin:2%;
    background: rgb(238, 238, 238);
    display: flex;  
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    padding: 0.4rem;
    box-sizing: border-box;
    border-radius: 0.5rem;
}.bottom{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
.search-content{
    width: 9rem;
    height: 9rem;
}
.goodsimg{
    width: 100%;
    height: 100%;
}
.search{
    width:100%;
    margin-bottom:2rem;
}
.content{
    height: 100%;
    width: 100%;
    overflow: scroll;
    padding-left:1rem;
    padding-right:1rem;
    box-sizing: border-box;
}
.top{
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    top:0px;
    height: 2rem;
}
.back{
    height: 2rem;
    border-radius: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>
<script>
import axios from 'axios';
import detail from "../components/detail.vue"
    export default {
        data(){
            return {
                dialogVisible:false,
                searchtext:"",
                goods:[],
                detail:{
                    detail:false,
                    direction:"rtl",
                    data:{

                    }
                }
            }
        },methods:{
            handleClose(done){
                done();
            },back(){
                this.search.detail=false;
            },searchgoods(searchtext){
                    axios.get("/searchgoods",{
                        params:{
                            search:searchtext
                        }
                    }).then(res=>{
                        this.goods=res.data
                    })
            },open(id){
                 axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
                this.detail.detail=true;
               console.log(response.data)
            })
            }
            
        },props:{
            search:Object,
        },components:{detail},created(){
            console.log(this.search);
        },created(){

        },watch:{
          searchtext(newsearch){
                     if (this.timer) {
                    clearTimeout(this.timer)
                    }
                    this.timer = setTimeout(() => {
                    this.searchgoods(newsearch);
                    }, 1000)

            },
        },created(){
            this.searchgoods("");
        }
    }

</script>




