<template>
    <div class="shoucangbox">
        <div class="img-box"><img  class="img-box" :src="shoucang.img" @click="open(shoucang.id)"></div>
        <div class="text"><div class="tex">{{shoucang.name}}</div><i class="el-icon-star-off" @click="dialogVisible=true"></i></div>
        <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
         width="80%"
        :before-close="handleClose">
        <span>取消收藏 ?</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false;quxiao();">确 定</el-button>
        </span>
        </el-dialog>
        <detail :detail="{detail}" ref="hua" v-on:shuaxinshoucang="re()"></detail>
    </div>
</template>
<style scoped>
.tex{
    font-size: 0.5rem;
}
.text{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top:0.2rem;
}
.shoucangbox{
    width: 44%;
    height: 11rem;
    margin:3%;
}
.img-box{
    width:100%;
    height: 100%;
}
</style>
<script>
import axios from "axios"
import detail from '../detail.vue';
export default ({
    data() {
        return{
                dialogVisible:false,
                    detail:{
                    detail:false,
                    direction:"rtl",
                    data:{
                    }
                  }
        }
    },components: { detail },props:{
        shoucang:Object
    },methods:{
        re(){
            this.$emit("shuaxinshoucang");
        },
        handleClose(){},
        quxiao(){
            axios.get("/quxiaoshouchang",{
                params:{
                    id:this.shoucang.id
                }
            }).then(()=>{
                //回调父级执行刷新页面
                    this.$emit("getshouchang")
            })
        },open(id){
                  this.detail.detail=true;
                 axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
            })
            },
    },created(){
    }
})
</script>



