<template>
        <div class="dingdan-show">
            <div class="left">
                <div class="info">
                    <div class="name">{{goods.name}}</div>
                    <div class="time">{{goods.starttime}}</div>
                    <div class="price">￥{{goods.totalprice}}</div>
                </div>
                <div class="chakan" @click="searchdingdan()">查看订单</div>
            </div>
            <div class="right">
                <div >
                    <img class="dingdanimg" :src="goods.img">
                </div>
            </div>
    <dingdanview :dingdan="dingdan" v-on:getorder="shuaxinorder()"></dingdanview>
    </div>
</template>
<script>
import dingdanview from '../dingdanview.vue';
export default {
  components: { dingdanview },
  data() {
    return {
            dingdan:{
                   detail:false,
                   direction:"rtl",
                   data:{

                   }
            }
    };
  },
  props:{
    goods: Object,
  },methods:{
      searchdingdan(){  
            this.dingdan.detail=true;
            this.dingdan.data=this.goods;
            console.log(this.goods);
      },shuaxinorder(){
          this.$emit("getorder")
      }
  }
};
</script>
<style scoped>
.chakan{
    width:6rem;
    height: 2rem;;
    text-align: center;
    line-height: 2rem;
    border:1px solid rgb(179, 179, 179);
    font-size: 0.5rem;
    border-radius: 1.5rem;
}
.left{
    display: flex;
    flex-direction:column;
    justify-content: space-around;
}
.name{
    font-weight: 600;
    margin-bottom:0.8rem;
}
.time{
    font-size: 0.8rem;
    color:rgb(179, 179, 179);
    margin-bottom:0.8rem;
}
.dingdanimg{
    width:10rem;
    height: 10rem;
}
.dingdan-show{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
     background: linear-gradient(#fff, rgb(243, 242, 242));
     box-sizing: border-box;
}
</style>




