<template>
    <div class="content">
        <div class="showbox" >
            <div class="box-content" ref="showbox">
                <a class="img-content" v-for="item in showimg" :key="item.id"><img :src="item.img" class="img-content"></a>
            </div>
             <div class="loadbox">
                    <div class="now-load"></div>
                </div>
        </div>
    </div>
</template>
<style scoped>
.loadbox{
    background:rgba(192, 190, 190,0.6);
    height:0.3rem;
    width:80%;
    position: absolute;
    bottom:1rem;
}
.now-load{
    width: 0%;
    background: white;
    height: 100%;
}
.content{
    position: relative;
}
.img-content{
    height:100%;
    background:pink;
}
.showbox{
    width:100%;
    height:20rem;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.box-content{
    position: absolute;
    height:100%;
    display: flex;
    flex-direction: row;
    left:0px;
}
</style>
<script>
import axios from 'axios';
 export default{
     data(){
         return{
             startx:0,
             nowx:0,
             showimg:[]
         }
     },methods:{

     },
     created(){
         //swiper初始化需要在axios执行完成后调用
                 axios.get("/getswiper").then(res=>{
                 this.showimg=res.data.showimg;}).then(res=>{
        //滑动初始化,参数设置以及监听函数添加
         var startx=0;
         var startleft=0;
         var nowx=0;
         var endx=0;
         var cbox=(Number)($(".showbox").css("width").substring(0, $(".showbox").css("width").length-2));

         $(".img-content").css({width:cbox+"px"})
         window.onresize =()=>{
             cbox =(Number)($(".showbox").css("width").substring(0, $(".showbox").css("width").length-2));
                $(".img-content").css({width:cbox})
         }
         var boxwidth=(Number)($(".box-content").css("width").substring(0,$(".box-content").css("width").length-2));
         var box=(Number)($(".showbox").css("width").substring(0,$(".showbox").css("width").length-2));
        $(".now-load").animate({width:cbox/boxwidth*100+"%"});
          this.$refs.showbox.addEventListener("touchstart",function(){
               var e = event||window.event;
                        startx=e.changedTouches[0].pageX;

                        startleft=(Number)($(".box-content").css("left").substring(0,$(".box-content").css("left").length-2));
             })
             this.$refs.showbox.addEventListener("touchmove",function(){
                  var e = event||window.event;
                        nowx=e.changedTouches[0].pageX
                       var  swip=nowx-startx;
                     var max= -1*(Number)($(".box-content").css("width").substring(0,$(".box-content").css("width").length-2))+box;
                       if(startleft+swip>0||startleft+swip<max)
                       {
                           swip/=4;
                       }
                       $(".box-content").css({left:startleft+swip});
             })
                 this.$refs.showbox.addEventListener("touchend",function(){   
                  var e = event||window.event;
                  endx=e.changedTouches[0].pageX;
                  var tar=startx-endx;
                  var left=(Number)($(".box-content").css("left").substring(0,$(".box-content").css("left").length-2));
                  var endleft=0;
                  //
                  if(left<0){
                      left*=-1;
                  }
                  if(tar<0){
                      //往右滑
                        if(left%box<box/1.3){
                        endleft=Math.floor(left/box);
                        }
                        else{
                                endleft=Math.floor(left/box)+1;
                        }
                  }
                  else{
                        if(left%box>box/3){
                        endleft=Math.floor(left/box)+1;
                        }
                        else{
                                endleft=Math.floor(left/box);
                        }
                  }
                  if(endleft>0){
                      endleft*=-1;
                  }
                   $(".box-content").animate({left:endleft*box});
                   //滑动结束判断
                   //进度条
                   var load=((endleft*-1)+1)*cbox/boxwidth*100+"%";
                   $(".now-load").animate({width:load});
             })
                 })
     },
     mounted(){
       
     }
     
 }
</script>


