<template>
    <div class="content">
        <img class="content" :src="dc.item.img">
        <div class="text-d" v-if="dc.item.range==1" style="top:0px;left:0px;text-align: start;">
            <div  v-for="i in dc.item.textarr" v-bind:key="i" class="text">{{i}}</div>
        </div>
         <div class="text-d" v-if="dc.item.range==2" style="top:0px;right:0px;text-align: end;">
            <div  v-for="i in dc.item.textarr" v-bind:key="i" class="text">{{i}}</div>
        </div>
    </div>
    
</template>
<style scoped>
.content{
    width:100%;
    height:30rem;
    border-radius: 4%;
    position: relative;
    margin-top:0.6rem;
}
.text{
    color:white;
    padding-top:0.1rem;
    padding-bottom:0.1rem;

}
.text-d{
    position: absolute;
    padding:1rem;
    box-sizing: border-box;
}
</style>
<script>
export default {
    props:{
        dc:{
            type:Object,
            default:{
            }
        }
    },
    data(){
        return {

        }
    },methods:{
        
        
    }
    ,created(){
    }
}
</script>


