import Vue from 'vue'
import Router from 'vue-router'
import home from '@/views/home'
import search from '@/views/search'
import haibao from '@/views/haibao'
import cart from '@/views/cart'
import user from '@/views/user'
import notlogin from '@/views/notlogin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/haibao',
      name: 'haibao',
      component: haibao
    },
    {
      path: '/cart',
      name: 'cart',
      component: cart
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/notlogin',
      name: 'notlogin',
      component: notlogin
    },
  ],
})



