<template>
<div class="">
        <el-drawer
        :visible.sync="detail.detail.detail"
        :direction="detail.detail.direction"
        :show-close="false"
         size= 100%
        :before-close="handleClose"
        >   
        <div class="content">
        <div class="top">
            <div class="left">
                <div class="back" @click="back()">
                    <i class="el-icon-arrow-left"></i>
                </div>
            </div>
        </div>
            <div class="img-content">
                <div class="img-box">
                    <img :src="detail.detail.data.img" class="img-c"> 
                    <div class="bottom-box">
                        <div class="b-box"><img class="b-img" :src="detail.detail.data.img"></div>
                        <div class="b-box"><img class="b-img" :src="detail.detail.data.img"></div>
                        <div class="b-box"><img class="b-img" :src="detail.detail.data.img"></div>
                            <div class="b-box"><img class="b-img" :src="detail.detail.data.img"></div>
                    </div>
                </div>
            </div>
            <div class="huadong" @touchmove="touch()" @touchstart="start()">
                <div class="b-content">
            <div class="ic"><div class="t-line"></div></div>
                <div class="id">{{detail.detail.data.number}}</div>
            <div class="fa"> <h2>{{detail.detail.data.name}}</h2>
            <i class="el-icon-star-on" style="font-size:2.4rem" v-if="detail.detail.data.shoucang==true" @click="shoucang();quxiaoshoucangmsg()"></i>
            <i class="el-icon-star-off" style="font-size:2rem" v-if="detail.detail.data.shoucang==false" @click="shoucang();shoucangmsg()"></i>
            </div>
                <div class="price">￥{{detail.detail.data.price}}</div>
                <div class="detai">
                    Vrgil Abloh向复古篮球鞋汲取灵感,为路易威登呈现 LV Trainer运动鞋，成就鞋履系列的经典，Monogram丹宁布构型搭配Monogram花卉魔术贴鞋带，繁复鞋面需要品牌工匠耗时7小时方可完成.
                </div>
                <el-collapse accordion style="background: rgb(240, 239, 239);">
                <el-collapse-item>
                    <template slot="title">
                    一致性 Consistency<i class="header-icon el-icon-info"></i>
                    </template>
                    <div>与现实生活一致：与现实生活的流程、逻辑保持一致，遵循用户习惯的语言和概念；</div>
                    <div>在界面中一致：所有的元素和结构需保持一致，比如：设计样式、图标和文本、元素的位置等。</div>
                </el-collapse-item>
                <el-collapse-item title="反馈 Feedback">
                    <div>控制反馈：通过界面样式和交互动效让用户可以清晰的感知自己的操作；</div>
                    <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div>
                </el-collapse-item>
                <el-collapse-item title="效率 Efficiency">
                    <div>简化流程：设计简洁直观的操作流程；</div>
                    <div>清晰明确：语言表达清晰且表意明确，让用户快速理解进而作出决策；</div>
                    <div>帮助用户识别：界面简单直白，让用户快速识别而非回忆，减少用户记忆负担。</div>
                </el-collapse-item>
                <el-collapse-item title="可控 Controllability">
                    <div>用户决策：根据场景可给予用户操作建议或安全提示，但不能代替用户进行决策；</div>
                    <div>结果可控：用户可以自由的进行操作，包括撤销、回退和终止当前操作等。</div>
                </el-collapse-item>
                </el-collapse>
            </div>
            </div>
            <div class="bottom">
                <div class="b">
                        <router-link class="cart" to="cart">
                        <div class="cart-box">
                            <i  class="el-icon-goods" style="font-size:3rem" @click="tocart()"></i>
                         <div class="number">{{cartnumber}}</div>
                        </div>
                    </router-link>
                    <div class="addcart" @click="addtocart.drawer=true;addtocarted();">放入购物袋</div>
                </div>
            </div>
            <div class="a" style="display:none"  :value="detail.detail.data.id">{{detail.detail.data.id}}</div>
          
        </div>
    </el-drawer>
     <addtocart :addtocart="addtocart" @getnumber="getnumber()"></addtocart>
    </div>
</template>
<style scoped>
.number{
        position: absolute;
        top:1.2rem;
        left:1.2rem;
        font-size: 1rem;
        font-weight: 700;
}
.cart-box{
    position: relative;
}
.b{
    display: flex;
    flex-direction: row;
}
.bottom{
    width: 100%;
    position: fixed;
    bottom:0px;
    display: flex;
    flex-direction: row;
    background: white;
    justify-content:flex-end;
}
.addcart{
    width: 8rem;
    height: 3rem;
    color:black;
    border:2px solid black;
    background: white;
    margin-left: 1rem;
    text-align: center;
    line-height: 3rem;
    border-radius: 0.8rem;
}
.xuangou{
        width: 8rem;
    height: 3rem;
    color:white;
    border:2px solid white;
    background: black;
    text-align: center;
    line-height: 3rem;
    border-radius: 0.8rem;
    margin-left: 1rem;
}
.b-content{
        padding-left:1rem;
    box-sizing: border-box;
}
.detai{
    margin-top:2rem;
    color:rgb(163, 159, 159)
}
.fa{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right:1rem;
    box-sizing: border-box  ;

}
.ic{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top:1rem;
}
.t-line{
    width: 5rem;
    height: 3px;
    background: rgb(156, 153, 153);
}
.huadong{
    width: 100%;
    border-radius: 1rem;
    background: white;
    position: relative;
    top:0px;
    padding-bottom:3rem;
    box-sizing: border-box;
}

.b-box{
    height: 6rem;
    width: 6rem;
}
.b-img{
    width: 100%;
    height:100%;
}
.bottom-box{
    display: flex;
    position: absolute;
    box-sizing: border-box;
    bottom:1rem;
    padding-left:2rem;
    padding-right:2rem;
    width: 100%;;
}
.content{
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.top{
    display: flex;
    flex-direction: row;
    padding-left:1rem;
    padding-right:3rem;
    box-sizing: border-box;
}
.back{
    width: 2rem;
    height: 2rem;
    border-radius: 2rem;
    background:rgb(202, 199, 199);
    display: flex;
    justify-content: center;
    align-items: center;
}
.img-c{
    height: 90%;
    width: 100%;

}
.img-box{
    width: 100%;
        position: relative;
    height:28rem;
}
</style>
<script>
import axios from 'axios';
import addtocart from "./addtocart.vue";
 export default {
     data(){
         return {
             starty:0,
             startboxy:0,
             shouchang:false,
             cartnumber:0,
             addtocart:{
                drawer:false ,
                direction: 'btt',
                data:{
                }
            },
         }
        },props:{
            detail:Object,
        },
     created(){
         this.getnumber();
     }
     ,components:{addtocart},
     methods:{
         quxiaoshoucangmsg(){
             this.$message('取消收藏');
         },
         shoucangmsg(){
              this.$message('收藏成功');
         },
         tocart(){
             this.$emit("getcart");
            if(this.$route.path=="/cart"){
                    this.back();
            }
         },
         handleClose(){
             done();
         },touch(){
              var e = event||window.event;
              var x=this.starty-(Number)(e.changedTouches[0].pageY);
              var nowx=(Number)($(".huadong").css("top").substring(0,$(".huadong").css("top").length-2));
              var height=(Number)($(".huadong").css("height").substring(0,$(".huadong").css("height").length-2));
              if(nowx>0&&x<0){
              }
              else{
                  if(this.startboxy<0)
                  {
                         if((nowx*-1)<(height-140)&&x>=0){
                               $(".huadong").css({top:-1*(-1*this.startboxy+x)+"px"})
                         }
                         if(x<0){
                            $(".huadong").css({top:-1*(-1*this.startboxy+x)+"px"})
                         }
                  }
                  else{
                      if((nowx*-1)<(height-140))
                      {
                        $(".huadong").css({top:-1*(this.startboxy+x)+"px"})
                      } 
                  }
              }
           
         },getnumber(){
            axios.get("/getcartinfo").then(res=>{
                this.cartnumber=res.data.length
                //console.log(res.data.length);
            })
         }
         ,start(){
               var e = event||window.event;
             this.starty=e.changedTouches[0].pageY;
             this.startboxy=(Number)($(".huadong").css("top").substring(0,$(".huadong").css("top").length-2));
         },back(){
           this.detail.detail.detail=false;
         },shoucang(){
             this.detail.detail.data.shoucang=!this.detail.detail.data.shoucang;
             axios.get("/updateshoucang",{
                        params: {
                        id: this.detail.detail.data.id
                        }
                    }).then(res=>{
                       this.$emit("shuaxinshoucang");
             })
         },addtocarted(){
             axios.get("/getdetail",{
                 params:{
                    id:(Number)($(".a").text())
                 }
             }).then(res=>{
                this.addtocart.data=res.data;
             })
         },
     },mounted(){
        
     },
 }

</script>




