<template>
        <el-drawer
        :title="addtocart.data.number"
        :visible.sync="addtocart.drawer"
        :direction="addtocart.direction"
        :show-close="false"
         size= 60%
        :before-close="handleClose"
        >   
        <div class="b-content">
            <div class="topa">
                <div class="t-imgbox">
                    <img class="cart-img" :src="addtocart.data.img" >
                </div>
                <div class="infobox">
                    <div class="color">颜色</div>
                    <div class="price">￥{{addtocart.data.price}}</div>
                </div>
            </div>
            <div class="mid-box">尺码选择:<i class=""></i></div>
            <div class="sizebox">
                <el-button v-bind:class="{ 'bind': (item==5.5) }"  v-for="item in addtocart.data.sizechoose"  :key="item" class="sieze" v-on:click="change($event)" :value="item">{{item}}</el-button>
            </div>
            <div class="bottom-button" @click="queding();">确定</div>
        </div>
    </el-drawer>
</template>
<script>
import axios from 'axios';

export default {
    data() {
        return{
              direction: 'btt',
               dbox:true,
               updatesize:5.5,
               cartnumber:0,
        }
     },props:{
            addtocart:Object,
        },created(){
     },
     methods:{
                msg(){
              this.$message('添加成功');
         },
         handleClose(){
         this.addtocart.drawer=false;
         },
           change(event){
                 this.updatesize=event.currentTarget.value
                 $(".sieze").css({background:"white"});
                 $(".sieze").css({color:"black"});
               event.currentTarget.style.background="black";
               event.currentTarget.style.color="white";
            },queding(){
                this.addtocart.drawer=false;
                axios.get("/addtocart",{
                    params:{
                        id:this.addtocart.data.id,
                        choose:this.updatesize
                    }
                }).then(res=>{
                    console.log(res);
                }).then(()=>{
                    this.msg();
                      axios.get("/getcartinfo").then(res=>{
                          this.cartnumber=res.data.length;
                          this.$emit("getnumber")
                      })
             })  
            }
    },mounted(){
      
    }
}
</script>
<style scoped>
.bind{
    background: black;
    color: white;
}
.b-content{
    height: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box;
}
.topa{
    display: flex;
    justify-content: start;
}
.cart-img{
    height:5rem;
    width: 5rem;
    background: black;
}
.mid-box{
    height: 2rem;
}
.bottom-button{
    position:absolute;
    bottom:2rem;
    width: 92%;
    height: 3rem;
    border-radius: 0.5rem;
    background: black;
    color:white;
    text-align: center;
    line-height:3rem;
    margin:auto
}
.sieze{
    width:22%;
    margin:1%;
    border:1px solid rgb(177, 172, 172);
    border-radius: 1.5rem;
    text-align: center;
    font-size: 1rem;
    height:3rem;
}
.sieze:focus{
    background: white;
    color: black;
}
.sizebox{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}
.infobox{
    margin-left:2rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
</style>


