<template>
<div class="">
            <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
         width="80%"
        :before-close="handleClose">
        <span>取消订单 ?</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false;quxiaodingdan();">确 定</el-button>
        </span>
        </el-dialog>
          <div class="dingdan">
              <el-drawer
        :visible.sync="dingdan.detail"
        :direction="dingdan.direction"
        :show-close="false"
         size= 100%
        :before-close="handleClose"
        >   
        <div class="content">
        <div class="top">
                <div class="back" @click="back()">
                    <i class="el-icon-arrow-left"></i>订单详情
            </div>
        </div>
        <div class="textinfo">
            <div class="texttop">
            <h2 v-if="dingdan.data.iswait==true">等待支付</h2>
            <h2 v-if="dingdan.data.iswait==false">订单关闭</h2>
            <div  v-if="dingdan.data.iswait==true" class="quxiao" @click="quxiao() ">取消订单</div>
        </div>
            <div class="dingdanid t">订单号:{{dingdan.data.orderid}}</div>
            <div class="xiadantime t">开始时间: {{dingdan.data.starttime}}</div>
            <div class="quxiaotime t" v-if="dingdan.data.iswait==false">结束时间: {{dingdan.data.endtime}}</div>
        </div>
       <div class="order">
           <div class="goods" v-for="item in dingdan.data.data" :key="item.id" >
                <div class="left"><img  class="img" :src="item.data.img"></div>
                <div class="right">
                    <div class="inline"><div class="name">{{item.data.name}}</div><div class="price">￥{{item.data.price}}</div></div>
                    <div class="inline"><div class="id">{{item.data.number}}</div></div>
                    <div class="r"><div class="number">x1</div></div>
                    <div class="inline"><div class="size">尺码:{{item.choose}}</div></div>
                </div>
           </div>
       </div>
       <div class="a">   
            <div class="inline">
              <h3>订单金额</h3>
              <h3>￥{{dingdan.data.totalprice}}</h3>
          </div>
       </div>
        <div class="peisonginfo">
            <i class="el-icon-location-outline" style="font-size:1.6rem"></i>
            <div class="addressinfo">
                <div class="black">配送信息</div>
                <div class="black">你的名字 17302730669</div>
                <div class="gray tto">上海市 上海市 黄浦区</div>
                <div class="gray tto">天虹街道 天虹新城 17栋 1801</div>
            </div>
        </div>
         <div class="jingxuantuijian" >
            <div class="j-title">精选推荐</div>
          <huakuai ></huakuai>
        </div>
        </div>
        <div class="bottom" v-if="dingdan.data.iswait==true"><div class="bottom-button">前往支付</div>
        </div> 
    </el-drawer>
    </div>
</div>
  
</template>
<style scoped>
.bottom-button{
    width: 90%;
    margin: auto;
    background: black;
    color: white;
    text-align: center;
    height: 3rem;
    line-height: 3rem;
}
.bottom{
    padding: 1rem;
    box-sizing: border-box;
    background: white;
    width: 100%;
    position: fixed;
    bottom:0px;
}
.j-title{
    font-size: 1.4rem;
    font-weight: 700;
}
.jingxuantuijian{
    width:100%;
    background: white;
    padding:1rem;
    box-sizing: border-box;
}
.tto{
    padding-top:0.3rem;

}
.gray{
    font-size: 0.8rem;
}
.black{
    font-weight: 700;;
}
.peisonginfo{
     border-bottom: 1px solid rgb(168, 168, 168);
     display: flex;
     flex-direction: row;
     padding-bottom:1rem;
     padding-top:1rem;

}
.addressinfo{
    padding-left:1rem;
    box-sizing: border-box;

}
.a{
        border-bottom: 1px solid rgb(168, 168, 168);
        margin-top:1rem;
        margin-bottom:1rem;
}
.r{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
}
.id{
    font-size: 0.7rem;
    color:rgb(168, 168, 168);
}
.size{
      font-size: 0.7rem;
    color:rgb(168, 168, 168);
}
.number{
         font-size: 0.7rem;
    color:rgb(168, 168, 168);
}
.name{
    font-weight: 700;
}
.price{
    font-size: 0.8rem;
}
.order{
    display: flex;
    flex-direction: column  ;
        border-bottom: 1px solid rgb(168, 168, 168);
}
.goods{ 
        width: 100%;

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-bottom:0.5rem;
        box-sizing: border-box;
}   
.inline{
         width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
}
.right{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
      padding-bottom:1rem;
      padding-top:1rem;
      padding-left:1rem;
        box-sizing: border-box;
}
.left{
    width:30%;
}
.img{
    width: 100%;
}
.textinfo{
    width: 100%;
    border-bottom: 1px solid rgb(168, 168, 168);
    padding-bottom:0.5rem;
    box-sizing: border-box;
}
.t{
    font-size: 0.4rem;
    color:rgb(168, 168, 168);
    margin-bottom: 0.4rem;
}
.quxiao{
    border:1px solid rgb(168, 168, 168);

    padding:0.3rem;

}
.texttop{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.content{
    height: 100%;
    width: 100%;
    overflow: scroll;
    padding-left:1rem;
    padding-right:1rem;
    padding-bottom:6rem;
    box-sizing: border-box;
}
.top{
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    top:0px;
    height: 2rem;
}
.back{
    height: 2rem;
    border-radius: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>
<script>
import axios from 'axios';
import huakuai from "../components/showgoods/huakuai.vue";
    export default {
        data(){
            return {
                dialogVisible:false
            }
        },methods:{
            handleClose(done){
                done();
            },back(){
                this.dingdan.detail=false;
            },quxiao(){
                this.dialogVisible=true;
                 
            },quxiaodingdan(){
                   axios.get("/deletedingdan",{
                        params:{
                            orderid:this.dingdan.data.orderid
                        }
                    }).then(()=>{
                           this.dingdan.detail=false;
                            this.$emit("getorder");
                    })
            }
        },props:{
            dingdan:Object,
        },components:{huakuai},created(){
            console.log(this.dingdan);
        }
    }

</script>



