<template>
    <div class="user-content">
        <div class="top">
            <h2>HI</h2>
             <h1>{{name}}</h1>
            <div class="tab-bottom">
                <div class="btext" v-for="item in tabtext" :key="item.value"  @click="tab(item.value)">{{item.text}}</div>
            </div>
        </div>
            <div class="users-content">
                <div class="show">
                    <div class="contaner">
                        <div class="tab">
                            <div class="order">
                                  <div class="e-botton" v-if="dingdan.length==0">
                                        <router-link class="bar" to="/search">
                                            <div class="empty-botton">前往选购</div>
                                        </router-link>
                             </div>
                            <dingdan v-for="item in dingdan" :key="item.orderid" :goods="item" v-on:getorder="getdingdan()"></dingdan>
                            </div>
                            <div class="inline"><div class="address">地址管理</div><i class="el-icon-right"></i></div>
                            <div class="inline"><div class="address">条款政策</div><i class="el-icon-right"></i></div>
                            <div class="inline"><div class="address">FAQ</div><i class="el-icon-right"></i></div>
                            <div class="inline"><div class="address">个性化推荐说明</div><i class="el-icon-right"></i></div>
                        </div>
                        <div class="tab" >
                            <div class="shoucang-c">
                                <div class="center" v-if="shoucang.length==0">     
                                     <h3>暂无收藏商品</h3>
                                    <div class="e-botton" >
                                        <router-link class="bar" to="/search">
                                            <div class="empty-botton">前往收藏</div>
                                        </router-link>
                                    </div></div>
                                    
                                <shoucang v-on:getshouchang="getshoucang()" v-for="item in shoucang" :key="item.id" :search="{item}" :shoucang="item" v-on:shuaxinshoucang="getshoucang()" ></shoucang>
                            </div>

                        </div>
                        <div class="tab" style="background: green;" >123</div>
                    </div>
                </div>
            </div>
        </div>
</template>
<style scoped>
.center{
    width: 100%;
    display: flex;
    align-content:center;
    align-items: center;
    justify-content: center;
    flex-direction:column;
}
.empty-botton{
    color:white;
    background: black;
    width:10rem;
    height:3rem;
    border-radius: 2rem;
    line-height: 3rem;
    text-align: center;
}
.e-botton{
    display: flex;
    align-items: center;
    justify-content: center;
}
h2{
    margin: 0px;
}
h1{
        margin: 0px;
}
.inline{
    width: 90%;
    margin: auto    ;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 1rem;
    padding-bottom: 1rem;
    box-sizing: border-box;
    border-bottom:2px solid rgb(117, 117, 117)
}
.user-content{
    width: 100%;
    overflow: hidden;
    height: 100%;
}
.user-content::-webkit-scrollbar{width:0;}
.shoucang-c{
    display: flex;
    flex-wrap: wrap;
}
.c{
    color:black !important;
}
.tab{
    width: 100%;
    height: 100%;
    overflow: scroll;
    padding-bottom: 8rem;
    box-sizing: border-box;
}
.tab::-webkit-scrollbar{width:0;}
.users-content{
    width:100%;
    height: 30rem;
    margin-top:2rem;
        border-top-left-radius: 1.8rem;
    border-top-right-radius: 1.8rem;
}
.show{
    overflow: hidden;
    width:100%;
    height: 100%;
    position: relative;
}
.contaner{
    width: 300%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: row;
    left: 0px;
}
.top{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    overflow: scroll    ;
}
.top::-webkit-scrollbar{width:0;}
.btext{
    color:gray;
    font-weight: 800;
    font-size: 1rem;
}
.tab-bottom{
    width: 100%;
    padding-left:10%;
    padding-right:10%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    line-height: 1rem;
    margin-top:2rem;
}
</style>
<script>
import axios from 'axios';
import shoucang from '../components/showgoods/shoucang.vue';
import dingdan from '../components/showgoods/dingdan.vue';
    export default {
  components: { shoucang,dingdan },
        data(){
            return{
                  name:"你的名字",
                  tabtext:[{text:"我的订单",value:1},{text:"我的鞋墙",value:2}],
                  shoucang:[],
                  dingdan:[],
            }
        },methods:{
             islogin(){
                 if(localStorage.getItem("user")==null)
                {
                   this.$router.push({name:'notlogin'})
                }
            },
            tab(value){
                $(".btext").removeClass("c");
                $(".btext").eq(value-1).addClass("c");
              var x=(Number)($(".show").css("width").substring(0,$(".show").css("width").length-2));
               $(".contaner").animate({left:-1*(value-1)*x});
               this.nowbox=value;
            },
            
            getshoucang(){

            axios.get("/getshoucang").then(res=>{
                this.shoucang=res.data;
            })
            },getdingdan(){
                console.log("回调");
                axios.get("/getorderinfo").then(res=>{
                    this.dingdan=res.data;
                });
            }
        },mounted(){
            $(".btext").eq(0).addClass("c");
            this.getdingdan();

        },componets:{shoucang},created(){
            this.islogin();
            this.getshoucang()
        },
    }
</script>





