<template>
    <div class="huakuai-content">
        <detai :detail="{detail}" v-on:getcart="tocart()"></detai>
        <div class="showbox" >
            <div class="box-content" ref="showbox">
                <tuijiangoods  v-for="item in search" :key="item.id" :search="{item}" @click.native="open(item.id)"></tuijiangoods>
            </div>
             <div class="loadbox">
                 <div class="coat" style="position: relative;"> <div class="now-load"></div></div>
                </div>
        </div>
    </div>
</template>
<style scoped>
.loadbox{
    background:rgba(17, 17, 17, 0.6);
    height:0.3rem;
    width:90%;
    position: absolute;
    bottom:1rem;
}
.coat{
    width:100%;
    height:100%
}
.now-load{
    background: white;
    height: 100%;
    position: absolute;
    left: 0px;

}
.huakuai-content{
    position: relative;
}
.showbox{
    width:100%;
    height:18rem;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.box-content{
    position: absolute;
    height:100%;
    display: flex;
    flex-direction: row;
    left:0px;
}
</style>
 <script>
 import tuijiangoods from "./tuijiangoods.vue"
 import detai from "../detail.vue"
import axios from 'axios'
    export default {
        data(){
            return{
            startx:0,
             nowx:0,
             search:
             [],
               detail:{
                   detail:false,
                   direction:"rtl",
                   data:{

                   }
            },
            }
        },components:{tuijiangoods,detai},
        mounted(){
            

             this.getgoods();
     },methods:{
         tocart(){
             this.$emit("getcart");
         },
         open(id){
                 axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
                  this.detail.detail=true;
            })
         },getgoods(){
             axios.get("/getgoods").then(res=>{
                 this.search=res.data.allgoods;
             }).then(()=>{
                          var startx=0;
         var startleft=0;
         var nowx=0;
         var cbox=(Number)($(".showbox").css("width").substring(0, $(".showbox").css("width").length-2));
         window.onresize =()=>{
             cbox =(Number)($(".showbox").css("width").substring(0, $(".showbox").css("width").length-2));
         }
         var boxwidth=(Number)($(".box-content").css("width").substring(0,$(".box-content").css("width").length-2));
         var box=(Number)($(".showbox").css("width").substring(0,$(".showbox").css("width").length-2));
         var lwidth=(Number)($(".loadbox").css("width").substring(0,$(".box-content").css("width").length-2));
        var c=cbox/boxwidth
        $(".now-load").animate({width:c*100+"%"});
          this.$refs.showbox.addEventListener("touchstart",function(){
               var e = event||window.event;
                        startx=e.changedTouches[0].pageX;
                        startleft=(Number)($(".box-content").css("left").substring(0,$(".box-content").css("left").length-2));
             })
             this.$refs.showbox.addEventListener("touchmove",function(){
                  var e = event||window.event;
                        nowx=e.changedTouches[0].pageX
                       var  swip=nowx-startx;
                     var max= -1*(Number)($(".box-content").css("width").substring(0,$(".box-content").css("width").length-2))+box;
                       if(startleft+swip>0||startleft+swip<max)
                       {
                       }
                       else{
                           $(".box-content").css({left:startleft+swip});
                           var x=startleft+swip;
                          if(x<0)
                          {
                              x*=-1;
                          }
                            var a=(lwidth*(1-c))*(x/(boxwidth-cbox));
                             $(".now-load").css({left:a});
                       }
                       
             })
             })
         }
     }

    }
 </script>
 


