<template>
        <div class="goods-show">
        <div class="goods-img">
            <img class="imgcontent" :src="goods.item.img"> 
        </div>
        <div class="goods-info">
            <div class="info">
                <div class="title">{{goods.item.name}}</div>
                <div class="price">￥{{goods.item.price}}</div>
            </div>
           <div class="buy-button">
                即刻选购
            </div>
        </div>
        
    </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props:{
    goods: Object,
    default:{

    }
  }
};
</script>

<style scoped>
.info{
    flex-direction: column;
}
.buy-button{
    width:5rem;
    height:2rem;
    border: 0.16rem solid black;
    border-radius: 1rem;
    padding-left:0.5rem;
    padding-right:0.5rem;
    line-height: 2rem;
      text-align: center;
}
.goods-show{
    width:88%;
    margin:auto;
    margin-top:1rem;
    margin-bottom:1rem;
}
.box-item{
    justify-content: center;
}
.goods-info{
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 0.8rem;
}
.imgcontent{
    height:18rem;
    width: 100%;
    background: #000;
}
</style>




