<template>
<a class="goods" :value="search.item.id">
    <img class="goodsimg" :src="search.item.img" >
    <div class="info">
         <div class="name">{{search.item.name}}</div>
    <div class="price">￥{{search.item.price}}</div>
    </div>
</a>
</template>
<style scoped>
.goodsimg{
    width: 100%;
    height: 70%;
    display: absolute;
}
.goods{
      width:11rem;
      height: 16rem;
      border-radius: 4%;
      margin-top:0.4rem;
      position: relative;
}
.info{
    width: 100%;
    height: 80%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    bottom:0px;
}
.name{
    font-weight: 600;
}
.price{
    margin-top:1rem;
}

</style>
<script>    
    export default {
          props:{
        search:{
            type:Object,
            default:{
                
            }
        }
    },
        data(){
            return{

            }
            
        }
    }

</script>


