<template>
<div class="cart-content" >
    <div class="" v-show="login">
          <div class="top" v-if="cartdata.length!=0">
        <div class="title">购物袋</div>
        <div class="mount">
            <div class="">共</div>
            <div class="number">{{cartdata.length}}</div>
            <div class="">件</div>
        </div>
    </div>
    <el-drawer
        :title="deletetex"
        :visible.sync="dbox"
        :direction="direction"
        :show-close="false"
         size= 20%
        :before-close="handleClosedelete"
        >   
          <div class="b-content">
            <div class="bottom-button" @click="deleteboxcart()">确认删除</div>
        </div>
    </el-drawer>
    <div class="cart-tent" v-if="cartdata.length!=0">
        <div class="goodscart" v-for="item in cartdata" :key="item.id">
            <div class="left">
                <input type="checkbox" class="check" @click="check" :value="item.data.price" :tabIndex="item.cartid">
                  <img  class="cart-img" :src="item.data.img"  @click="open(item.data.id)">
            </div>
            <div class="right">
                <div class="close"><i class="el-icon-close" @click="deletebox(item.cartid)"></i></div>
                <div class="goods-id">{{item.data.number}}</div>
                <div class="goods-name">{{item.data.name}}</div>
                <div class="goodsbottom">
                    <div class="pircebox">￥{{item.data.price}}</div>
                    <div class="sizechoose" @click="choose.drawer=true;sizechoose(item.cartid)">
                        <div class="chima">尺码</div>
                        <div class="size-box">{{item.choose}}</div>
                     <i class="el-icon-arrow-down"></i>
                </div>
                </div>
            </div>
        </div>
    </div>
        <div class="empty" v-if="cartdata.length==0">
            <div class="empty-text">您的购物袋为空</div>
        </div>
        <div class="e-botton" v-if="cartdata.length==0">
               <router-link class="bar" to="/search">
                <div class="empty-botton">前往选购</div>
               </router-link>
        </div>
        <div class="jingxuantuijian" v-if="cartdata.length==0">
            <div class="j-title">精选推荐</div>

          <huakuai v-show="cartdata.length==0" v-on:getcart="res()"></huakuai>
        </div>
          
    <div class="jiesuan" v-if="cartdata.length!=0">
        <div class="b-left">
                    <input type="checkbox" class="checkall" @click="checkall"> 
                       <div class="" style="margin-right:1rem">全选</div>
                    <div class="totalprice">￥{{totalprice}}</div>
                    </div>
        <div class="button"  @click="adtoorder()">立即结算</div>
    </div>
    <detail :detail="{detail}" v-on:getcart="res()"></detail>
    <sizechoose  :choose="{choose}" v-on:getcartinfo="res()"></sizechoose>
    </div>
</div>
 
</template>
<script>
import axios from 'axios'
import detail from "../components/detail.vue"
import huakuai from "../components/showgoods/huakuai.vue"
import sizechoose from "../components/sizechoose.vue"
    export default {
        data(){
            return{
                login:true,
            deletetex:"删除商品",
            drawer: false,
            goodsname:"LV TRAINER 运动鞋",
            choose:{
                drawer:false,
                direction: 'btt',
                data:{
                    number:1
                }
            },
            direction: 'btt',
            updatesize:0,
            totalprice:(Number)(0),
            delet:0,
            dbox:false,
             detail:{
                   detail:false,
                   direction:"rtl",
                   data:{

                   }
            },
            cartdata:[
                  
           ],
            }
        },
        methods:{
            //添加到购物车
            adtoorder(){
                   var check=$(".check");
                   var cart=[];
                   for(let i=0;i<check.length;i++)
                   {
                       if(check[i].checked==true)
                       {
                            cart.push(check[i].tabIndex);
                       }
                   }
                   axios.get("/addtoorder",{
                       params:{
                           cart:cart,
                           totalprice:this.totalprice,
                       }
                   }).then(res=>{
                       //更新购物车
                       this.$message("已添加到我的订单");
                       this.res();
                   })
            },
            deleteboxcart(){
                   this.dbox=!this.dbox;
                     axios.get("/deletecart",{
                        params:{
                            cartid:this.delet
                        }
                    }).then(()=>{
                            this.res();
                    }).then(()=>{
                        this.deletemsg();
                          this.price();
                    })
            }
            ,deletemsg(){
                this.$message('删除成功');
            },
            deletebox(id){
                    this.dbox=!this.dbox;
                    this.delet=id;
            },
            handleClosedelete(done){
                  done();
            },
            //弹窗关闭
             handleClose(done) {
                 done();
             },
             //尺码选择弹框参数传递
             sizechoose(cartid){
                 axios.get("/updatesize",{params: {
                        cartid:cartid
                     }}
                     ).then(res=>{
                         this.choose.data=res.data;
                 })
             },
            check(){
                var check=$(".check");
                var all=0;
               for(let i=0;i<check.length;i++)
               {
                   if(check[i].checked){
                       all++;
                   }
               }
                if(all==check.length){
                    $(".checkall")[0].checked=true;

                }
                else{
                    $(".checkall")[0].checked=false;

                }
                this.price();
            },
            //全选点击
            checkall(){
                   var check=$(".check");
                if($(".checkall")[0].checked==true)
                {
                      for(let i=0;i<check.length;i++)
                    {
                        check[i].checked=true;
                    }
                }
                else{
                       for(let i=0;i<check.length;i++)
                    {
                        check[i].checked=false  ;
                    }
                }
               this.price();
              
            },
            //计算总价
            price(){
                this.totalprice=0;
                var check=$(".check")
                  for(let i=0;i<check.length;i++)
               {
                   if(check[i].checked==true){
                       this.totalprice+=(Number)(check[i].value);
                   }
               }
               if(this.totalprice==0){
                   $(".button").removeClass("enable");
                   $(".button").addClass("disable");
                   $(".button").css({background:"gray"});
               }
               else{    
                   $(".button").removeClass("disable");
                    $(".button").addClass("enable");
                  $(".button").css({background:"black"});
               }    
            },open(id){
                  this.detail.detail=true;
                 axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
            })
           
            }
            ,res(){
                console.log("刷新");
                            axios.get("/getcartinfo").then(res=>{
                    this.cartdata=res.data
                    this.choose.data=this.cartdata[0];
                }).then(()=>{
                        if(this.cartdata.length!=0)
                        {
                            var check=$(".check");
                            $(".checkall")[0].checked=true;
                            for(let i=0;i<check.length;i++){
                                $(".check")[i].checked=true;
                            }
                            this.price();
                        }
                })
            }
        },mounted(){
        },components:{detail,huakuai,sizechoose},
        created(){
                this.res();
        }
    }
</script>
<style scoped>
.disable{
  pointer-events: none;
}
.enable{
  pointer-events: auto;
}
.jingxuantuijian{
    margin-top:4rem;
    width:100%;
    background: white;
    padding:1rem;
    box-sizing: border-box;
}
.j-title{
    font-size: 1.4rem;
    font-weight: 700;
}
.e-botton{
    display: flex;
    align-items: center;
    justify-content: center;
}
.empty-botton{
    color:white;
    background: black;
    width:10rem;
    height:3rem;
    border-radius: 2rem;
    line-height: 3rem;
    text-align: center;
}
.empty-text{
    font-size: 1.8rem;
    text-align: center;
    font-weight: 700;
}
.close{
    position: absolute;
    right:0.5rem;
    top:0.5rem;
    font-size: 1.2rem;
    font-weight: 700;
}
.empty{
    width:100%;
    margin-bottom: 3rem;
}
.mid-box{
    height: 2rem;
}
.topa{
    display: flex;
    justify-content: start;
}
.infobox{
    margin-left:2rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
.sieze{
    width:22%;
    margin:1%;
    border:1px solid rgb(177, 172, 172);
    border-radius: 1.5rem;
    text-align: center;
    font-size: 1rem;
    height:3rem;
}
.sieze:focus{
    background: white;
    color: black;
}
.b-content{
    height: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box;
}
.bottom-button{
    position:absolute;
    bottom:2rem;
    width: 92%;
    height: 3rem;
    border-radius: 0.5rem;
    background: black;
    color:white;
    text-align: center;
    line-height:3rem;
    margin:auto
}
.sizebox{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.content{
    height:100%;
    
}
.chima{
    color: rgb(177, 172, 172);
    font-size: 0.8rem;
    height:1rem;
}
.size-box{
    font-size: 1rem;
    padding-left:0.2rem;
    padding-right:0.2rem;
}
   .btt{
      height:50% !important;
    }
.sizechoose{
    height:2rem;
    width:6.8rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    padding-left:1rem;
    padding-right:1rem;
    justify-content: space-between;
    border:1px solid gray;
    margin-right: 1rem;
    border-radius: 1rem;
}
.goods-chouti{
   height:20rem;
}
.right{
    width:70%;
    height:70%;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content:space-evenly
}
.goodsbottom{
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pircebox{
        font-weight: 700;
}
.goods-name{
    font-weight: 700;
    font-size: 1rem;
}
.b-left{
    display: flex;
    flex-direction: row;
}
.button{
    width:4rem;
    background: black;
    padding-left:2.6rem;
    padding-right:2.6rem;
    padding-top:1rem;
    padding-bottom:1rem;
    color:white;
}
.jiesuan{
    position: fixed;
    width:100%;
    height:6rem;
    background: white;
    bottom:4rem;
    border-radius: 8%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding-left:2rem;
    padding-right:2rem;
    align-items: center;
}
.left{
    height:100%;
    display: flex;
    align-items: center;
    position: relative;
}
.goodscart{
    height:100%;
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}
.cart-img{
    height:5rem;
    width: 5rem;
    background: black;
}
.check{
    width:20px;
    height:20px;
    position: absolute;
    top:0.5rem;
}
.checkall{
      width:20px;
    height:20px;
    margin-right:1rem;
}
.cart-content{
    height: 100%;
}
.goodscart{
    width: 88%;
    height:12rem;
    border-radius: 2%;
    background: white;
    margin-top:0.5rem;
}
.cart-tent{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height:100%;
    padding-bottom:2rem;
    box-sizing: border-box;
    padding-top:1rem;
}
.number{
    line-height: 1.4rem;
}
.title{
    font-size: 1.6rem;
    font-weight: 700;
    margin-right: 0.8rem;
    margin-left: 1rem;
}
.top{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;

}   
.mount{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
        font-weight: 700;
    font-size: 1rem;
}
</style>




