<template>
        <el-drawer
        :title="choose.choose.data.data.number"
        :visible.sync="choose.choose.drawer"
        :direction="choose.choose.direction"
        :show-close="false"
         size= 60%
        :before-close="handleClose"
        >   
        <div class="b-content">
            <div class="topa">
                <div class="t-imgbox">
                    <img class="cart-img" :src="choose.choose.data.data.img" >
                </div>
                <div class="infobox">
                    <div class="color">颜色</div>
                    <div class="price">￥{{choose.choose.data.data.price}}</div>
                </div>
            </div>
            <div class="mid-box">尺码选择:<i class=""></i></div>
            <div class="sizebox">
                <el-button  v-bind:class="{ 'bind': (item==choose.choose.data.choose) }" v-for="item in choose.choose.data.data.sizechoose"  :key="item" class="sieze" v-on:click="change($event)" :value="item">{{item}}</el-button>
            </div>
            <div class="bottom-button" @click="queding()">确定</div>
        </div>
    </el-drawer>
</template>
<script>
import axios from 'axios';

export default {
    data() {
        return{
              direction: 'btt',
               dbox:false,
               choosenumber:0,
        }
     },props:{
            choose:Object,
        },created(){
     },
     methods:{
         handleClose(done){
          done();
         },
           change(event){
                 this.choosenumber=event.currentTarget.value
                 $(".sieze").css({background:"white"});
                 $(".sieze").css({color:"black"});
               event.currentTarget.style.background="black";
               event.currentTarget.style.color="white";
            },queding(){
                    axios("/size",{
                    params:{
                        cartid:this.choose.choose.data.cartid,
                        choose:this.choosenumber
                    }
                }).then(res=>{
                        console.log(res);
                }).then(()=>{
                       this.$emit("getcartinfo")
                       this.$message("修改成功");
                })

                this.choose.choose.drawer=false
            }
    },mounted(){
        this.choosenumber=this.choose.choose.data.choose;
    }
}
</script>
<style scoped>
.bind{
    background: black;
    color: white;
}
.b-content{
    height: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box;
}
.topa{
    display: flex;
    justify-content: start;
}
.cart-img{
    height:5rem;
    width: 5rem;
    background: black;
}
.mid-box{
    height: 2rem;
}
.bottom-button{
    position:absolute;
    bottom:2rem;
    width: 92%;
    height: 3rem;
    border-radius: 0.5rem;
    background: black;
    color:white;
    text-align: center;
    line-height:3rem;
    margin:auto
}
.sieze{
    width:22%;
    margin:1%;
    border:1px solid rgb(177, 172, 172);
    border-radius: 1.5rem;
    text-align: center;
    font-size: 1rem;
    height:3rem;
}
.sieze:focus{
    background: white;
    color: black;
}
.sizebox{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}
.infobox{
    margin-left:2rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
</style>



