<template>
    <div class="cart-content">
        <div class="">
        <cart></cart>
        </div>
    </div>
</template>
<style scoped>
.cart-content{
    height: 100%;
    overflow: scroll;
    position: relative;
}
.cart-content::-webkit-scrollbar{width:0px};

</style>
<script>
import cart from "../components/cart.vue"

    export default {
        data(){
            return{

            }
        },components:{cart},
        created(){
              this.islogin();

        },methods:{
            islogin(){
                 if(localStorage.getItem("user")==null)
                {
                   this.$router.push({name:'notlogin'})
                }
            }
        }
    }
</script>


