<template>
<div class="home-content">
    <div class="">
    <swiper></swiper>
    <goods @click.native="open(item.id)" v-for="item in goodsall" :key="item.id"  :goods="{item}"></goods>
    <detail :detail="{detail}" ref="hua"></detail>
</div>
    
    </div>
</template>
<script>
import goods from "../components/showgoods/goods.vue"
import swiper from "../components/showgoods/swiper.vue"
import huakuai from "../components/showgoods/huakuai.vue"
import detail from "../components/detail.vue"
import axios from 'axios'
export default {
    data(){
        return{
            goodsall:[],   
            detail:{
                   detail:false,
                   direction:"rtl",
                   data:{

                   }
            }
        }
    },components:{goods,swiper,detail,huakuai},
    mounted(){

    },created(){
        axios.get("/getgoods").then(res=>{
          this.goodsall=res.data.allgoods;
        })
    }
    ,methods:{
        open(id){
          this.detail.detail=true;
          this.getdetail(id);
        }, 
        getdetail(id){
            axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
            })
        }    
    }
}
</script>
<style scoped> 
.home-content{
    border-top-left-radius: 1.8rem;
    border-top-right-radius: 1.8rem;
    height: 100%;
    overflow-x:hidden; overflow-y:scroll;
    scrollbar-width: none;
    
}
.home-content::-webkit-scrollbar{width:0;}
</style>



