<template>
<div class="topna">
	<div class="topbox">
		<div class="topleft">
			<div class="logobox">
				<i class="el-icon-eleme"></i>
			</div>
			<div class="line"></div>
			<div class="searchicon">
				<i class="el-icon-search" @click="searchopen()"></i>
			</div>
		</div>
		<div class=""></div>
		<div class="topleft">
			<div class="logobox">
				<i class="el-icon-eleme"></i>
			</div>
			<div class="line"></div>
			<div class="searchicon">
				<i class="el-icon-search"></i>
			</div>
		</div>
	</div>
	<search :search="search" ></search>
</div>
</template>

<script>
import search from "../search.vue";
export default {
	data() {
		return {
			 search:{
				detail:false,
				direction:"rtl",

           	 }
		}
	},methods:{
		searchopen(){
			this.search.detail=true;

		}
	},components:{search}
}
</script>

<style>
.topbox{
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-left:0.4rem;
	padding-right:0.4rem;
	justify-content: space-between;
}
.topna{
	height: 4rem;
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.logobox{
	padding-left: 0.3rem;
	padding-right: 0.3rem;

}
.searchicon{
	padding-left: 0.3rem;
	padding-right: 0.3rem;
	
}
.topleft{
	display: flex;
	width: 18%;
	height: 1.4rem;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	border: 0.1px solid gray;
	border-radius: 1rem;
	box-sizing: border-box;
}
.line{
	 height: 60%;
	 width: 1px;
	
	background: gray;
}
</style>


