function getorderinfo(){
    var re=[];
    for(let i=0;i<order.order.length;i++){
        var orderinfo={}
        orderinfo.starttime=order.order[i].starttime;
        orderinfo.orderid=order.order[i].orderid;
        orderinfo.endtime=order.order[i].endtime;
        orderinfo.iswait=order.order[i].iswait;
        orderinfo.goods=order.order[i].goods;
        orderinfo.totalprice=order.order[i].totalprice;
        var data=[];
        for(let i=0;i<orderinfo.goods.length;i++){
            for(let j=0;j<cart.cart.length;j++){
                if(orderinfo.goods[i]==cart.cart[j].cartid){
                    var odata={}
                    odata.data=a(cart.cart[j].id);
                    odata.choose=cart.cart[j].choose;
                    data.push(odata);
                }
            }
        }
        orderinfo.data=data;
       orderinfo.img=orderinfo.data[0].data.img+"";
       orderinfo.name=orderinfo.data[0].data.name;
       re.push(orderinfo);
       console.log(orderinfo);
    }
    return re;
}
var ordermax=10000;
var ordercontrol=setInterval(endorder,1000*3);
function endorder(){
    var nowdate = Date.parse(new Date());
    for(let i=0;i<order.order.length;i++)
    {
        if(order.order[i].endtime<nowdate&&order.order[i].iswait==true)
        {
            console.log("change")
            order.order[i].iswait=false;
            order.order[i].endtime=new Date().toLocaleString().replace(/年|月/g, "-").replace(/日/g, " ").replace(/上午|下午/g,"");
        }
    }
}

Mock.mock(RegExp('/searchgoods?' + ".*"), 'get',function(options){
    var decode=decodeURI(options.url);
    var searchtext=(decode.split('?')[1].split("=")[1]).toLowerCase()
    return searchgoods(searchtext)
})
function searchgoods(searchtext){
    var rep=RegExp(searchtext);
    var returndata=[];
    for(let i =0;i<allgoods.allgoods.length;i++)
    {
        if(allgoods.allgoods[i].name.toLowerCase().match(rep)){
                returndata.push(allgoods.allgoods[i]);
        }
    }
console.log(returndata);
return returndata;
}
Mock.mock(RegExp('/deletedingdan?' + ".*"), 'get',function(options){
    var cartid=(Number)(options.url.split('?')[1].split("=")[1]);
    return deleteorder(cartid);
})
function deleteorder(orderid)
{
        for(let i=0;i<order.order.length;i++)
        {
                if(orderid==order.order[i].orderid)
                {
                    order.order.splice(i,1);
                }
        }
}
