//更改购物车尺码
Mock.mock(RegExp('/updatesize?' + ".*"), 'get',function(options){
    var id=options.url.split('?')[1].split('=')[1];
     return updatesize(id);
 })

 function updatesize(cartid){
    for(let i=0;i<cart.cart.length;i++)
    {
        if(cartid==cart.cart[i].cartid){
            var choose={}
            choose.data=a(cart.cart[i].id);
            choose.cartid=cart.cart[i].cartid;
            choose.choose=cart.cart[i].choose
            return choose;
        }
    }
}
