Mock.mock(RegExp('/size?' + ".*"), 'get',function(options){
    console.log(options.url)
    var parmas=options.url.split('?')[1].split('&');
    var id=parmas[0].split('=')[1];
    var size=parmas[1].split('=')[1];
     return getcart(id,size);
 })
 function getcart(cartid,size){
    for(let i=0;i<cart.cart.length;i++)
    {
        if(cartid==cart.cart[i].cartid){
            cart.cart[i].choose=size;
            break;
        }
    }
    console.log(cart.cart);
    return true;
}
