Mock.mock('/getcartinfo', 'get', function(){
    return getcartinfo();
} )
function getcartinfo(){
    var cartinfo=[];
    for(let i=0;i<allgoods.allgoods.length;i++){
        for(let j=0;j<cart.cart.length;j++){
            if(cart.cart[j].id==allgoods.allgoods[i].id&&cart.cart[j].isorder==false){
                var item = new Object();
                item.data=a(allgoods.allgoods[i].id);
                item.choose=cart.cart[j].choose;
                item.cartid=cart.cart[j].cartid;
                cartinfo.push(item);
            }   
        }
    }
    return cartinfo;
}
