Mock.mock(RegExp('/addtocart?' + ".*"),'get', function(options){
    var parmas=options.url.split('?')[1].split('&');
    console.log(parmas);
    var id=parmas[0].split('=')[1];
    var choose=parmas[1].split('=')[1];
    addtocart(id,choose)
    return  true;
} )
var cartmax=1000;
function addtocart(id,choose){
    if(cart.cart.length==0)
    {

    }else{
        cartmax=cart.cart[cart.cart.length-1].cartid;
    }   

    var a={};
    a.id=id;
    a.choose=choose;
    a.cartid=++cartmax;
    a.isorder=false;
    cart.cart.push(a);
}
