<template>
    <div class="search-content">    
        <div class="search">
        <searchgood v-for="item in search" :key="item.id" :search="{item}" @click.native="showdetial(item.id)"></searchgood>
    </div>
    <detail :detail="{detail}" ref="hua"></detail>
    </div>  
</template>
<script>
import searchgood from "../components/showgoods/searchgood.vue"
import detail from "../components/detail.vue"
import axios from "axios"
export default {
    data(){
        return{
            search:[],
            detail:{
                   detail:false,
                   direction:"rtl",
                   data:{
                   }
            }
        }
    },
    components:{searchgood,detail},
    created(){
        axios.get("/getgoods").then(res=>{
            this.search=res.data.allgoods;
        })
    },
    mounted(){
    },
    methods:{
        handleClose(){
            done();
        },
        showdetial(id){
            this.detail.detail=true;
            this.getdetail(id);
        },
        getdetail(id){
            axios.get('/getdetail', {
                params: {
                id: id
                }
            }) .then(response=> { 
                this.detail.data=response.data
                console.log(response);
            })
        }     
    }
}
</script>
<style scoped>

.search{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;

}
.search-content{
    height: 100%;
    padding-left:1.8rem;
    padding-right:1.8rem;
    overflow: scroll;

}
.search-content::-webkit-scrollbar{width:0;}
</style>



